}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
  font-size: calc(10px + 2vmin);
  color: white;
  width: auto;
  background-color: rgb(95, 15, 170);
  border-color: blue;
  border-radius: 1px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
